import { AnimationClip } from "cc";
import { SPIKE_COUNT_ENUM } from "../../Enum";
import { StateMachine } from "../Base/SateMachine";
import State from "../Base/State";
import { SubStateMachine } from "../Base/SubStateMachine";



const BASE_URL = 'texture/spikes/spikesone/'
const BASE_END = '/spikesone'
export default class SpikesOneSubStateMachine extends SubStateMachine {
    constructor(fsm: StateMachine) {
        super(fsm)

        this.stateMachine.set(SPIKE_COUNT_ENUM.ZERO, new State(fsm, `${BASE_URL}zero` + BASE_END))
        this.stateMachine.set(SPIKE_COUNT_ENUM.ONE, new State(fsm, `${BASE_URL}one` + BASE_END))
        this.stateMachine.set(SPIKE_COUNT_ENUM.TWO, new State(fsm, `${BASE_URL}two` + BASE_END))
    }

    run() {

    }
}